<template>
    <button :class="classObject">
        <slot></slot>
    </button>
</template>

<script>
    export default {
        props: {
            type: String,
            text: String,
            isSaving: String,
            theme: String,
            isDisabled: {
                type: Boolean,
                default: false
            },
            isLoading: {
                type: Boolean,
                default: false
            },
        },
        computed: {
            classObject: function() {
                return {
                    "isSaving": false,
                    "flat-button": true,
                    [this.theme]: true
                }
            }
        }
    }
</script>

<style scoped>
    .flat-button {
        width: 100%;
        padding: 10px;
        font-weight: bold;
        text-align: center;
        border-radius: 5px;
        border: none;
        transition: background-color .2s ease-in;
    }
    .flat-button.primary {
        background-color: #00cece;
        color: white;
    }
    .flat-button:hover {
        background-color: #00adad;
    }
</style>